/* Slider */

.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}
.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;

  &:focus {
      outline: none;
  }

  &.dragging {
      cursor: pointer;
      cursor: hand;
  }
}
.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;

  &:before,
  &:after {
      content: "";
      display: table;
  }

  &:after {
      clear: both;
  }

  .slick-loading & {
      visibility: hidden;
  }
}
.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  [dir="rtl"] & {
      float: right;
  }
  img {
      display: block;
  }
  &.slick-loading img {
      display: none;
  }

  display: none;

  &.dragging img {
      pointer-events: none;
  }

  .slick-initialized & {
      display: block;
  }

  .slick-loading & {
      visibility: hidden;
  }

  .slick-vertical & {
      display: block;
      height: auto;
      border: 1px solid transparent;
  }
}
.slick-arrow.slick-hidden {
  display: none;
}

.slider {
  padding: 40px 20px;

  &__title {
    display: flex;
    justify-content: center;
    position: relative;
    font-family: 'Roboto', sans-serif;
    font-size: 28px;
    line-height: 30px;
    font-weight: 700;
    color: #2e2e2e;
    margin: 0 0 40px;

    &::after {
      position: absolute;
      background-color: #D30000;
      width: 74px;
      height: 5px;
      bottom: -25px;
      margin-left: -38px;
      left: 50%;
      content: '';
      z-index: 1;
    }
  }
}

@media screen and (max-width: 1390px) {
  .slider {
    &__title {
      font-size: 18px;
      line-height: 20px;
    }

    img {
      max-height: 350px;
    }
  }
}

@media screen and (max-width: 1140px) {
  .slider {
    &__title {
      font-size: 16px;
      line-height: 18px;

      &::after {
        bottom: -12px;
        height: 3px;
      }
    }

    img {
      max-height: 300px;
    }
  }
}

@media screen and (max-width: 930px) {
  .slider {
    padding: 20px 50px;

    &__title {
      margin: 0 0 25px;
    }
  }
}

@media screen and (max-width: 850px) {
  .slider {
    padding: 20px 20px;

    &__title {
      font-size: 14px;
      line-height: 16px;
    }
  }
}

@media screen and (max-width: 730px) {
  .slider {
    padding: 20px 50px;
  }
}

@media screen and (max-width: 620px) {
  .slider {
    padding: 20px 50px;

    &__title {
      font-size: 12px;
      line-height: 14px;

      &::after {
        bottom: -10px;
        height: 1px;
      }
    }
  }
}

@media screen and (max-width: 520px) {
  .slider {
    padding: 20px 15px;
  }
}
