::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: $backgroundDefault;
  box-shadow: 0 0 2px rgba( 0, 0, 0, 1) inset;
}

::-webkit-scrollbar-thumb {
  background: $colorLightGrey;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background: #757474;
}
